@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

body {
  @apply bg-white text-gray-800 dark:bg-gray-900 dark:text-white;
}

/* Tool Card Animations */
.tool-card {
  @apply opacity-0 bg-gray-200 border border-white dark:bg-gray-800 dark:border-gray-700 dark:text-gray-100;
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;
  animation: fadeIn 0.5s ease forwards;
}

.tool-card:hover {
  @apply transform scale-105 shadow-lg border-blue-500 dark:border-blue-500/50;
}

.tool-card:nth-child(1) { animation-delay: 0.1s; }
.tool-card:nth-child(2) { animation-delay: 0.2s; }
.tool-card:nth-child(3) { animation-delay: 0.3s; }
.tool-card:nth-child(4) { animation-delay: 0.4s; }
.tool-card:nth-child(5) { animation-delay: 0.5s; }

/* Link Hover Effects */
.get-tool {
  @apply relative text-gray-800 dark:text-gray-100;
  transition: color 0.2s ease;
}

.get-tool:hover {
  @apply text-blue-700 dark:text-blue-400;
}

.get-tool::after {
  content: '';
  @apply absolute left-0 bottom-0 h-px w-0 bg-blue-500;
  transition: width 0.2s ease;
}

.get-tool:hover::after {
  @apply w-full;
}

/* Modal Styles */
.modal {
  @apply fixed inset-0 bg-black/50 flex justify-center items-center z-[1000];
}

.modal-content {
  @apply bg-white dark:bg-gray-800 p-6 rounded text-lg text-center max-w-md w-[90%];
}

.modal-content h2 {
  @apply text-xl mb-4;
}

.modal-content p {
  @apply text-base text-gray-600 dark:text-gray-300 mb-6;
}

.modal-content button {
  @apply bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-400 dark:hover:bg-blue-600;
}

/* Reusable Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slide-down {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

@layer utilities {
  .animate-fade-in {
    @apply opacity-0;
    animation: fadeIn 0.3s ease-in-out forwards;
  }

  .animate-slide-down {
    animation: slide-down 0.3s ease-out;
  }
}

/* Miscellaneous */
.category-section {
  scroll-margin-top: 80px;
}

